@import "../../sharedStyles";

.profileHeader{
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 30px;
  @include mainPageComponentsStyles;
  &__background{
    position: relative;
    img{
      height: 300px;
      width: 100%;
      object-fit: cover;
      border-radius: 5px 5px 0 0;
    }
  }
  &__menu{
    position: relative;
    width: 100%;
    height: 100px;
    border-radius: 0 0 5px 5px;
  }
  &__contacts{
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex;
    gap: 5px;
  }
}
.profile-short{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 300px;
  bottom: 10px;
  right: 50%;
  margin-right: -150px;
  img{
    box-sizing: border-box;
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-radius: 50%;
    border: 6px solid #ffffff;
  }
  p{
    margin: 10px 0 0 0;
    text-align: center;
  }
  a{
    text-decoration: none;
  }
  &__name{
    font-size: 20px;
    font-weight: 700;
    color: $titleTextColor;

  }
}